.header-menu{
    width: 100%;
    max-width: 1500px;
    height: var(--height-header);
    background-color: var(--white);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 1px 5px rgb(0 0 0 / 10%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}
.header-brand{
    width: 210px;
}
.nav-menu{
    width: auto;
    height: auto;
}
.nav-menu-link{
    font-family: var(--font-alt);
    font-size: var(--size-menu);
    margin: 0 0 0 40px;
    text-decoration: none;
    color: var(--gray);
    -webkit-transition: color 0.5s;
    -moz-transition: color 0.5s;
    -o-transition: color 0.5s;
    letter-spacing: 1px;
}
.nav-menu-link:hover{
    color: var(--pink);
}
.active{
    color: var(--pink);
}
.link-cv{
    padding: 5px 15px;
    background-color: var(--pink);
    margin: 0 0 0 25px;
    text-decoration: none;
    color: var(--white);
    font-family: var(--font-alt);
    border-radius: 5px;
    font-size: var(--size-menu);
    -webkit-transition: opacity 0.5s;
    -moz-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    letter-spacing: 1px;
}
.link-cv:hover{
    color: var(--white);
    opacity: 0.7;
}
.icon-download{
    font-size: 13px;
}



