.projects-container{
    width: 100%;
    max-width: 1500px;
    height: auto;
    background-color: var(--white-alt);
    margin: var(--height-header) 0 0 0;
}
.title-project{
    width: 100%;
    height: auto;
    padding: 40px 30px 0 30px;
}
.title-project h2{
    margin: 0 0 5px 0;
    text-transform: uppercase;
    color: var(--black);
}
.title-project p{
    margin: 0;
    font-size: var(--size-paragraph);
    font-family: var(--font-paragraph);
    line-height: var(--line-height);
    color: var(--gray);
}
.projects{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 25px 30px 30px 30px;
}
.project{
    width: 32.5%;
    height: auto;
    position: relative;
    box-shadow: 0 4px 10px 5px rgb(0 0 0 / 20%);
    border-radius: 5px;
    margin: 0 0 20px 0;
    overflow: hidden;
}
.project-image{
    width: 100%;
    height: auto;
    border-radius: 5px;
    -webkit-transition: transform 0.5s;
    -moz-transition: transform 0.5s;
    -o-transition: transform 0.5s;
}
.project-content{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0 0 0 / 70%);
    left: 0;
    top: 0;
    border-radius: 5px;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    opacity: 0;
    visibility: hidden;
    padding: 0 20px 0 20px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}
.project:hover .project-image{
    transform: scale(1.05);
}
.project:hover .project-content{
    opacity: 1;
    visibility: visible;
}
.project-title{
    color: var(--pink);
    font-size: var(--size-title-h3);
    font-family: var(--font-title);
    margin: 0;
}
.project-box-tecnology{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
}
.tecnology{
    background-color: var(--pink);
    margin: 0 3px 3px 0;
    font-family: var(--font-alt);
    font-size: 15px;
    color: var(--white);
    padding: 1px 10px;
    border-radius: 5px;
    font-weight: bold;
}
.icon-tecnology{
    font-size: 14px;
    margin: 0 5px 0 0;
}
.project-box-option{
    width: 100%;
    height: auto;
    display: flex;
    margin: 2px 0 0 0;
}
.project-box-option a{
    font-family: var(--font-alt);
    margin: 0 5px 0 0;
    padding: 3px 10px;
    border-radius: 3px;
    font-size: 15px;
    text-decoration: none;
    color: var(--white);
    border: 1px solid var(--white);
    -webkit-transition: color 0.5s, background-color 0.5s;
    -moz-transition: color 0.5s, background-color 0.5s;
    -o-transition: color 0.5s, background-color 0.5s;
}
.project-box-option a:hover{
    color: var(--pink);
    background-color: var(--white);
}





